import React, { Component } from "react";
import Banner from "../src/components/Banner/Banner";
import Chart from "../src/components/Chart/Chart";
import Instructions from "../src/components/Instructions/Instructions";
import ListHeaders from "../src/components/Analyzer/ListHeaders/ListHeaders";
import ListItems from "../src/components/Analyzer/ListItems/ListItems";
import SearchBar from "../src/components/Analyzer/SearchBar/SearchBar";
import SearchFilters from "../src/components/Analyzer/SearchFilters/SearchFilters";
import data from "../src/assets/data";
import sampledata from "../src/assets/sample";
import classes from "./DegreeAnalyzer.module.css";
import Degree from "../src/classes/Degree";
import Layout from "../src/hoc/Layout/Layout";
import Head from "next/head";
const fullDataset: any = data;

interface IDegreeAnalyzerState {
  input: string;
  filters: string[];
  filteredItems: Degree[];
}
class DegreeAnalyzer extends React.Component<{}, IDegreeAnalyzerState> {
  state: IDegreeAnalyzerState = {
    input: "",
    filters: ["NTU", "SMU"],
    filteredItems: sampledata
  };
  filterData = (data: Degree[], filters: string[], input: string) => {
    let filteredItems: Degree[] = [];
    //filter by school
    filters.forEach((key: any) => {
      filteredItems = filteredItems.concat(data[key]);
    });
    //filter by keyword
    return filteredItems.filter(
      item => item.degree.toLowerCase().indexOf(input) > -1
    );
  };

  addFilterHandler = (name: string) => {
    let currentFilters = this.state.filters;
    if (currentFilters.indexOf(name) === -1) {
      currentFilters.push(name);
      const input = this.state.input;
      let filteredItems = this.filterData(fullDataset, currentFilters, input);
      this.setState({ filters: currentFilters, filteredItems: filteredItems });
    }
  };
  removeFilterHandler = (name: string) => {
    let currentFilters = this.state.filters;
    if (currentFilters.indexOf(name) !== -1) {
      currentFilters.splice(currentFilters.indexOf(name), 1);
      const input = this.state.input;
      let filteredItems = this.filterData(fullDataset, currentFilters, input);
      this.setState({ filters: currentFilters, filteredItems: filteredItems });
    }
  };
  textChangedHandler = (event: any) => {
    const input = event.target.value.toLowerCase();
    const currentFilters = this.state.filters;
    let filteredItems = this.filterData(fullDataset, currentFilters, input);
    this.setState({ input: input, filteredItems: filteredItems });
  };
  keyPressHandler = (event: any) => {
    if (event.key === "Enter") {
      event.target.blur();
    }
  };

  render() {
    return (
      <Layout>
        <Head>
          <link
            href='https://fonts.googleapis.com/css?family=Open+Sans:300,400,700'
            rel='stylesheet'
          />
          <link
            href='https://fonts.googleapis.com/css?family=IBM+Plex+Mono:400,600'
            rel='stylesheet'
          />
          {/* <link href='index.css' rel='stylesheet' /> */}

          <title>GES 2018</title>
        </Head>
        <Banner />
        <Chart chartData={this.state.filteredItems} />
        <Instructions>Select the one or more schools to view.</Instructions>
        <SearchFilters
          addFilter={this.addFilterHandler}
          removeFilter={this.removeFilterHandler}
          filters={this.state.filters}
        />
        <Instructions>
          Use the search box to narrow down the results.
        </Instructions>
        <SearchBar
          changed={event => this.textChangedHandler(event)}
          keypress={event => this.keyPressHandler(event)}
        />

        <div className={classes.ResultsTable}>
          <ListHeaders />
          <ListItems items={this.state.filteredItems} />
        </div>
      </Layout>
    );
  }
}

export default DegreeAnalyzer;
